	import { BASE_URL } from '@/api'
	import { dealLogin } from '@/common/js/common'
	export default {
		name: '',
		props: {
			user_info: Object,
		},
		data () {
			return {
				orderTypeList: [
					{icon: '/static/images/a9@2x.png', text: '我的订单'},
					{icon: '/static/images/a13@2x.png', text: '待付款'},
					{icon: '/static/images/a10@2x.png', text: '待发货'},
					{icon: '/static/images/a11@2x.png', text: '待收货'},
					{icon: '/static/images/a12@2x.png', text: '待评价'},
					
				],
				activeList: [
					{text: '关注', num: '0'},
					{text: '粉丝', num: '0'},
					{text: '我的发布', num: '0'},
					// {text: '我的收藏', num: '0'},
				]
			}
		},
		onLoad () {

		},
		watch: {
			user_info () {
				this.$set(this.activeList[0], 'num', this.user_info.number.guanzhu)
				this.$set(this.activeList[1], 'num', this.user_info.number.fensi)
				this.$set(this.activeList[2], 'num', this.user_info.number.wodefabu)
				
				this.$set(this.orderTypeList[1], 'num', this.user_info.number.daifukuan)
				this.$set(this.orderTypeList[2], 'num', this.user_info.number.daifahuo)
				this.$set(this.orderTypeList[3], 'num', this.user_info.number.daishouhuo)
				this.$set(this.orderTypeList[4], 'num', this.user_info.number.daipingjia)
				// this.$set(this.orderTypeList[4], 'num', this.user_info.number.yiwancheng)
			},
		},
		computed: {
			BASE_URL () {
				return BASE_URL
			}
		},
		methods: {
			onCheckOrderClick (index) {
				if(!dealLogin()) {
					return;
				}
				uni.navigateTo({
					url: '/pages/mine/subPages/order/order?index=' + index
				})
			},
			onLookPersonalInfo () {
				if(!dealLogin()) {
					return;
				}
				uni.navigateTo({
					url: '/pages/mine/subPages/personal-info/personal-info'
				})
			},
			onCheckActiveClicen (index) {   // 跳转 关注、粉丝、我的发布
				if(!dealLogin()) {
					return;
				}
				if(index === 0) {
					uni.navigateTo({
						url: '/pages/mine/subPages/my-attention/my-attention'
					})
					return
				}
				if(index === 1) {
					uni.navigateTo({
						url: '/pages/mine/subPages/my-fans/my-fans'
					})
					return
				}
				if(index === 2) {
					uni.navigateTo({
						url: '/pages/mine/subPages/my-publish/my-publish'
					})
					return
				}
			},
			onPreviewImg (url) {
				if(!dealLogin()) {
					return
				}
				uni.previewImage({
           urls: [url],
					 current: url
        });
			}
		}
	}
